import uniIcons from '../uni-icons/uni-icons.vue'




export default {
	name: 'UniNoticeBar',
	components: {
		uniIcons
	},
	props: {
		text: {
			type: String,
			default: ''
		},
		moreText: {
			type: String,
			default: ''
		},
		backgroundColor: {
			type: String,
			default: '#fffbe8'
		},
		speed: {
			// 默认1s滚动100px
			type: [String, Number],
			default: 100
		},
		color: {
			type: String,
			default: '#de8c17'
		},
		moreColor: {
			type: String,
			default: '#999999'
		},
		single: {
			// 是否单行
			type: [String, Boolean],
			default: false
		},
		scrollable: {
			// 是否滚动，添加后控制单行效果取消
			type: [String, Boolean],
			default: false
		},
		showIcon: {
			// 是否显示左侧icon
			type: [String, Boolean],
			default: false
		},
		showGetMore: {
			// 是否显示右侧查看更多
			type: [String, Boolean],
			default: false
		},
		showClose: {
			// 是否显示左侧关闭按钮
			type: [String, Boolean],
			default: false
		}
	},
	data() {
		const elId = `Uni_${Math.ceil(Math.random() * 10e5).toString(36)}`
		const elIdBox = `Uni_${Math.ceil(Math.random() * 10e5).toString(36)}`
		return {
			textWidth: 0,
			boxWidth: 0,
			wrapWidth: '',
			webviewHide: false,



			elId: elId,
			elIdBox: elIdBox,
			show: true,
			animationDuration: 'none',
			animationPlayState: 'paused',
			animationDelay: '0s'
		}
	},
	mounted() {











		this.$nextTick(() => {
			this.initSize()
		})
	},





	methods: {
		initSize() {
			if (this.scrollable) {

				let query = [],
					boxWidth = 0,
					textWidth = 0;
				let textQuery = new Promise((resolve, reject) => {
					uni.createSelectorQuery()
						.in(this)
						.select(`#${this.elId}`)
						.boundingClientRect()
						.exec(ret => {
							this.textWidth = ret[0].width
							resolve()
						})
				})
				let boxQuery = new Promise((resolve, reject) => {
					uni.createSelectorQuery()
						.in(this)
						.select(`#${this.elIdBox}`)
						.boundingClientRect()
						.exec(ret => {
							this.boxWidth = ret[0].width
							resolve()
						})
				})
				query.push(textQuery)
				query.push(boxQuery)
				Promise.all(query).then(() => {
					this.animationDuration = `${this.textWidth / this.speed}s`
					this.animationDelay = `-${this.boxWidth / this.speed}s`
					setTimeout(() => {
						this.animationPlayState = 'running'
					}, 1000)
				})






























			}







		},
		loopAnimation() {























		},
		clickMore() {
			this.$emit('getmore')
		},
		close() {
			this.show = false;
			this.$emit('close')
		},
		onClick() {
			this.$emit('click')
		}
	}
}
